import { Component } from '@angular/core';

export class Item { 
    purchase: string;
    price: number;
    id: number;

    constructor(purchase: string, price: number, id: number) {
    	this.purchase = purchase;
    	this.price = price;
        this.id = id;
    }
}
@Component({
	selector: 'bill-app',
    templateUrl: "./app/app.html"
})
export class AppComponent { 
    items: Item[] = 
    [
    { purchase: "Хлеб", price: 1, id: 1 },
    { purchase: "Хлеб", price: 2, id: 2 },
    { purchase: "Хлеб", price: 3, id: 3 },
    { purchase: "Хлеб", price: 4, id: 4 }
    ];
    total: number = 10;
    addItem(text: string, price: number): void {

        if(text==null || text==undefined || text.trim()=="")
            return;
        if(price==null || price==undefined)
            return;
        this.items.push(new Item(text, price, this.items.length + 1));
        let total: number;
        total = 0;
        for (let i = 0; i < this.items.length; i++) {
        	total += this.items[i].price;
        }
        this.total = total;
    }
    deleteItem(id: number): void {
        console.log(this);
        console.log(id);
        if(id==null || id==undefined)
            return;
        this.total -= this.items[id-1].price;
        this.items.splice(id-1, 1);
        for (let key in this.items) {
            this.items[key].id = +key+1;
        }
    }
}